.redirect-page {
  position: fixed;
  z-index: 99999;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(150.14deg, #6480EB 0.6%, #433BC4 100%);
  opacity: 0.9;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column; }
  .redirect-page .dot1,
  .redirect-page .dot2 {
    background-color: #00D7AF !important; }
  .redirect-page .loading:after {
    content: ' .';
    animation: dots 1s steps(5, end) infinite; }

@keyframes dots {
  0%, 20% {
    color: rgba(0, 0, 0, 0);
    text-shadow: 0.25em 0 0 rgba(0, 0, 0, 0), 0.5em 0 0 rgba(0, 0, 0, 0); }
  40% {
    color: white;
    text-shadow: 0.25em 0 0 rgba(0, 0, 0, 0), 0.5em 0 0 rgba(0, 0, 0, 0); }
  60% {
    text-shadow: 0.25em 0 0 white, 0.5em 0 0 rgba(0, 0, 0, 0); }
  80%, 100% {
    text-shadow: .25em 0 0 white, .5em 0 0 white; } }
  .redirect-page img {
    background-color: white;
    border-radius: 25px;
    margin-bottom: 20px; }
  .redirect-page p {
    color: white;
    font-weight: bold; }
